import { 
  collection, 
  getDocs, 
  getDoc, 
  doc, 
  addDoc, 
  updateDoc, 
  deleteDoc,
  query,
  orderBy,
  where,
  setDoc,
  Timestamp
} from 'firebase/firestore';
import { db } from '../firebase';
import { v4 as uuidv4 } from 'uuid';

// Collection de rfrence
const FORM_LINKS_COLLECTION = 'formLinks';

// Assurez-vous que la collection existe
const ensureCollection = async (collectionName) => {
  try {
    // Vrifier si la collection existe en essayant de rcuprer des documents
    const collectionRef = collection(db, collectionName);
    const snapshot = await getDocs(query(collectionRef, orderBy('createdAt', 'desc')));
    
    // Si la collection n'existe pas ou est vide, c'est normal pour les liens de formulaire
    if (snapshot.empty) {
      console.log(`Collection ${collectionName} vide, prte  recevoir des liens de formulaire.`);
    }
    
    return true;
  } catch (error) {
    console.error(`Erreur lors de la vrification/cration de la collection ${collectionName}:`, error);
    return false;
  }
};

// Assurez-vous que la collection formLinks existe
const formLinksCollection = collection(db, FORM_LINKS_COLLECTION);

/**
 * Gnre un lien unique pour un formulaire
 * @param {string} concertId - ID du concert associ
 * @param {Object} concertData - Donnes du concert
 * @param {string} programmerId - ID du programmateur
 * @param {string} programmerName - Nom du programmateur
 * @returns {Promise<Object>} Lien gnr avec ID
 */
export const generateFormLink = async (concertId, concertData, programmerId, programmerName) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    // Vrifier si un lien existe dj pour ce concert
    const existingLink = await getFormLinkByConcertId(concertId);
    if (existingLink) {
      console.log(`Un lien existe dj pour le concert ${concertId}:`, existingLink);
      return existingLink;
    }
    
    // Gnrer un token unique
    const token = uuidv4();
    
    // Crer les donnes du lien
    const linkData = {
      concertId,
      concertName: concertData.artist?.name ? `${concertData.artist.name}  ${concertData.venue}` : `Concert  ${concertData.venue}`,
      concertDate: concertData.date || null,
      programmerId,
      programmerName,
      token,
      url: `/form/${token}`,
      createdAt: Timestamp.fromDate(new Date()),
      expiresAt: null, // Pas d'expiration pour l'instant
      isSubmitted: false,
      submissionId: null
    };
    
    console.log("Tentative de cration d'un lien de formulaire:", linkData);
    const docRef = await addDoc(formLinksCollection, linkData);
    
    console.log(`Lien de formulaire cr avec succs, ID: ${docRef.id}`);
    return {
      id: docRef.id,
      ...linkData
    };
  } catch (error) {
    console.error("Erreur lors de la cration du lien de formulaire:", error);
    
    // Essayer de crer le lien avec un ID gnr manuellement en cas d'erreur
    try {
      const mockId = 'mock-link-' + Date.now();
      const token = uuidv4();
      
      const linkData = {
        concertId,
        concertName: concertData.artist?.name ? `${concertData.artist.name}  ${concertData.venue}` : `Concert  ${concertData.venue}`,
        concertDate: concertData.date || null,
        programmerId,
        programmerName,
        token,
        url: `/form/${token}`,
        createdAt: Timestamp.fromDate(new Date()),
        expiresAt: null,
        isSubmitted: false,
        submissionId: null
      };
      
      await setDoc(doc(db, FORM_LINKS_COLLECTION, mockId), linkData);
      
      console.log(`Lien de formulaire cr avec un ID manuel: ${mockId}`);
      return {
        id: mockId,
        ...linkData
      };
    } catch (setError) {
      console.error("Erreur lors de la cration manuelle du lien de formulaire:", setError);
      
      // Simuler la cration d'un lien en cas d'erreur
      const token = uuidv4();
      const mockId = 'mock-link-' + Date.now();
      
      return {
        id: mockId,
        concertId,
        concertName: concertData.artist?.name ? `${concertData.artist.name}  ${concertData.venue}` : `Concert  ${concertData.venue}`,
        concertDate: concertData.date || null,
        programmerId,
        programmerName,
        token,
        url: `/form/${token}`,
        createdAt: new Date(),
        expiresAt: null,
        isSubmitted: false,
        submissionId: null
      };
    }
  }
};

/**
 * Rcupre un lien de formulaire par son ID
 * @param {string} id - ID du lien
 * @returns {Promise<Object>} Donnes du lien
 */
export const getFormLinkById = async (id) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log(`Tentative de rcupration du lien ${id} depuis Firebase...`);
    const docRef = doc(db, FORM_LINKS_COLLECTION, id);
    const snapshot = await getDoc(docRef);
    
    if (snapshot.exists()) {
      const linkData = {
        id: snapshot.id,
        ...snapshot.data()
      };
      console.log(`Lien ${id} rcupr depuis Firebase:`, linkData);
      return linkData;
    }
    
    console.log(`Lien ${id} non trouv dans Firebase`);
    return null;
  } catch (error) {
    console.error(`Erreur lors de la rcupration du lien ${id}:`, error);
    return null;
  }
};

/**
 * Rcupre un lien de formulaire par son token
 * @param {string} token - Token unique du lien
 * @returns {Promise<Object>} Donnes du lien
 */
export const getFormLinkByToken = async (token) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log(`Tentative de rcupration du lien avec le token ${token}...`);
    const linkQuery = query(
      formLinksCollection, 
      where('token', '==', token)
    );
    
    const snapshot = await getDocs(linkQuery);
    
    if (snapshot.empty) {
      console.log(`Aucun lien trouv avec le token ${token}`);
      return null;
    }
    
    const linkData = {
      id: snapshot.docs[0].id,
      ...snapshot.docs[0].data()
    };
    
    console.log(`Lien avec le token ${token} rcupr:`, linkData);
    return linkData;
  } catch (error) {
    console.error(`Erreur lors de la rcupration du lien avec le token ${token}:`, error);
    return null;
  }
};

/**
 * Rcupre un lien de formulaire par l'ID du concert associ
 * @param {string} concertId - ID du concert
 * @returns {Promise<Object>} Donnes du lien
 */
export const getFormLinkByConcertId = async (concertId) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log(`Tentative de rcupration du lien pour le concert ${concertId}...`);
    const linkQuery = query(
      formLinksCollection, 
      where('concertId', '==', concertId)
    );
    
    const snapshot = await getDocs(linkQuery);
    
    if (snapshot.empty) {
      console.log(`Aucun lien trouv pour le concert ${concertId}`);
      return null;
    }
    
    const linkData = {
      id: snapshot.docs[0].id,
      ...snapshot.docs[0].data()
    };
    
    console.log(`Lien pour le concert ${concertId} rcupr:`, linkData);
    return linkData;
  } catch (error) {
    console.error(`Erreur lors de la rcupration du lien pour le concert ${concertId}:`, error);
    return null;
  }
};

/**
 * Met  jour un lien de formulaire existant
 * @param {string} id - ID du lien
 * @param {Object} linkData - Nouvelles donnes du lien
 * @returns {Promise<Object>} Lien mis  jour
 */
export const updateFormLink = async (id, linkData) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log(`Tentative de mise  jour du lien ${id}:`, linkData);
    const docRef = doc(db, FORM_LINKS_COLLECTION, id);
    
    await updateDoc(docRef, linkData);
    
    console.log(`Lien ${id} mis  jour avec succs`);
    return {
      id,
      ...linkData
    };
  } catch (error) {
    console.error(`Erreur lors de la mise  jour du lien ${id}:`, error);
    
    // Essayer de crer/remplacer le document
    try {
      await setDoc(doc(db, FORM_LINKS_COLLECTION, id), linkData, { merge: true });
      
      console.log(`Lien ${id} cr/remplac avec succs`);
      return {
        id,
        ...linkData
      };
    } catch (setError) {
      console.error(`Erreur lors de la cration/remplacement du lien ${id}:`, setError);
      return null;
    }
  }
};

/**
 * Marque un lien comme soumis et l'associe  un ID de soumission
 * @param {string} id - ID du lien
 * @param {string} submissionId - ID de la soumission de formulaire
 * @returns {Promise<Object>} Lien mis  jour
 */
export const markFormLinkAsSubmitted = async (id, submissionId) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log(`Tentative de marquer le lien ${id} comme soumis avec la soumission ${submissionId}`);
    const docRef = doc(db, FORM_LINKS_COLLECTION, id);
    
    const updateData = {
      isSubmitted: true,
      submissionId,
      submittedAt: Timestamp.fromDate(new Date())
    };
    
    await updateDoc(docRef, updateData);
    
    console.log(`Lien ${id} marqu comme soumis avec succs`);
    return {
      id,
      ...updateData
    };
  } catch (error) {
    console.error(`Erreur lors du marquage du lien ${id} comme soumis:`, error);
    
    // Essayer de crer/remplacer le document
    try {
      const updateData = {
        isSubmitted: true,
        submissionId,
        submittedAt: Timestamp.fromDate(new Date())
      };
      
      await setDoc(doc(db, FORM_LINKS_COLLECTION, id), updateData, { merge: true });
      
      console.log(`Lien ${id} marqu comme soumis (via setDoc) avec succs`);
      return {
        id,
        ...updateData
      };
    } catch (setError) {
      console.error(`Erreur lors du marquage du lien ${id} comme soumis (via setDoc):`, setError);
      return null;
    }
  }
};

/**
 * Supprime un lien de formulaire
 * @param {string} id - ID du lien
 * @returns {Promise<string>} ID du lien supprim
 */
export const deleteFormLink = async (id) => {
  try {
    console.log(`Tentative de suppression du lien ${id}`);
    const docRef = doc(db, FORM_LINKS_COLLECTION, id);
    await deleteDoc(docRef);
    
    console.log(`Lien ${id} supprim avec succs`);
    return id;
  } catch (error) {
    console.error(`Erreur lors de la suppression du lien ${id}:`, error);
    return null;
  }
};

/**
 * Rcupre tous les liens de formulaire
 * @param {Object} filters - Filtres  appliquer (optionnel)
 * @returns {Promise<Array>} Liste des liens de formulaire
 */
export const getFormLinks = async (filters = {}) => {
  try {
    // S'assurer que la collection existe
    await ensureCollection(FORM_LINKS_COLLECTION);
    
    console.log("Tentative de rcupration des liens de formulaire depuis Firebase...");
    
    // Cration d'une requte de base
    let linkQuery = formLinksCollection;
    
    // Application des filtres si ncessaire
    if (filters) {
      // Filtrer par statut de soumission
      if (filters.isSubmitted !== undefined) {
        linkQuery = query(linkQuery, where('isSubmitted', '==', filters.isSubmitted));
      }
      
      // Filtrer par programmateur
      if (filters.programmerId) {
        linkQuery = query(linkQuery, where('programmerId', '==', filters.programmerId));
      }
      
      // Filtrer par concert
      if (filters.concertId) {
        linkQuery = query(linkQuery, where('concertId', '==', filters.concertId));
      }
    }
    
    // Ajout d'un tri par date de cration (du plus rcent au plus ancien)
    linkQuery = query(linkQuery, orderBy('createdAt', 'desc'));
    
    // Excution de la requte
    const snapshot = await getDocs(linkQuery);
    
    if (snapshot.empty) {
      console.log("Aucun lien de formulaire trouv dans Firebase");
      return [];
    }
    
    const formLinks = snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    }));
    
    console.log(`${formLinks.length} liens de formulaire rcuprs depuis Firebase`);
    return formLinks;
  } catch (error) {
    console.error("Erreur lors de la rcupration des liens de formulaire:", error);
    return [];
  }
};
